import React, {Component} from 'react';
import LogoFrame from './LogoFrame';
/* import $ from 'jquery'; */

class Logo extends Component {

    removeLogo = () => {
        this.props.disappear();
    }
    
    render() {
        return (
            <div id="logo" onClick={this.removeLogo}>
              <LogoFrame disappear={this.removeLogo} />
            </div>
        );
    }
}

export default Logo;
