import React, {Component} from 'react';

class LogoFrame extends Component {
    afterEffect = () => {
        setTimeout ( () => {
            this.props.disappear();
        }, 12200);
    }
    
    render() {
        return (
            <div id="logo-frame">
              <img onLoad={this.afterEffect}
                   src="/logo-anim.gif" alt="Hexadoon logo" />
            </div>
        );
    }
}

export default LogoFrame;
